"use client";

import useWindowDimension from "@/hooks/useWindowDimension";
import { useState, useEffect, useRef } from "react";
import Sidebar from "./Sidebar";

const Navbar = () => {
  const { scrollY, width } = useWindowDimension();
  const [isScrolled, setIsScrolled] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const sideBarRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (scrollY > 50) {
      setIsScrolled(true);
    } else {
      setIsScrolled(false);
    }
  }, [scrollY]);

  const onSideBar = () => {
    setIsOpen(!isOpen);
  };

  const internalSideBarCloseState = (data: string) => {
    if (data === "side_bar") {
      setIsOpen(false);
    }
  };

  const handleSideBarOnClick = (event: MouseEvent) => {
    if (
      sideBarRef.current &&
      !sideBarRef.current.contains(event.target as Node)
    ) {
      setIsOpen(false);
    }
  };

  useEffect(() => {
    document.addEventListener("mousedown", handleSideBarOnClick);

    return () => {
      document.removeEventListener("mousedown", handleSideBarOnClick);
    };
  }, []);

  return (
    <>
      {/* sidebar */}
      <Sidebar
        ref={sideBarRef}
        isOpen={isOpen}
        onCloseButton={internalSideBarCloseState}
      />

      {/* navbar */}
      <nav
        className={`z-100 block w-full fixed top-0 transition-all duration-300 transform translate-z-0 ${
          isScrolled ? "bg-white/80 bg-opacity-30 backdrop-blur-md" : ""
        }`}
      >
        <div
          className={`wrap px-[36px] max-md:px-[16px] max-w-[1920px] mx-auto bg-transparent`}
        >
          <div
            className={`flex justify-between min-h-[57px] ${
              isScrolled ? "items-start" : "items-start"
            }  w-full px-0 py-[12px]`}
          >
            <div
              className="left flex items-center space-x-4 min-h-[30px] cursor-pointer"
              onClick={onSideBar}
            >
              <span>
                <div className="flex flex-col justify-between items-center w-5 h-5 bg-transparent border-0">
                  <span className="block w-full h-[3px] bg-black"></span>
                  <span className="block w-full h-[3px] bg-black"></span>
                  <span className="block w-full h-[3px] bg-black"></span>
                </div>
              </span>
              <span className="font-regular-title-bold">MENU</span>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
